/*
	Shadows

	Shadows can be easily added to container .shadow_{{modifier}}  

	shadow_n1			- inset shadow
	shadow_n2			- inset shadow
	shadow_n3			- inset shadow
	shadow_n4			- inset shadow
	shadow_n5			- inset shadow
	shadow_0			- no shadow
	shadow_1			- box shadow
	shadow_2			- box shadow
	shadow_3			- box shadow
	shadow_4			- box shadow
	shadow_5			- box shadow

	markup:
	<div class="{$modifiers} padding_3 border_solid border_black-1 border-width_1 color_white">container has shadow of {$modifiers}</div>

	Styleguide 81.6
 
 */
.shadow_0 {
  box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0);
}

.shadow {
  box-shadow: 1.5px 1.5px 6px 0.75px rgba(0, 0, 0, 0.4);
}

.shadow_1 {
  box-shadow: 0.5px 0.5px 2px 0.25px rgba(0, 0, 0, 0.1);
}

.shadow_2 {
  box-shadow: 1px 1px 4px 0.5px rgba(0, 0, 0, 0.2);
}

.shadow_3 {
  box-shadow: 1.5px 1.5px 6px 0.75px rgba(0, 0, 0, 0.3);
}

.shadow_4 {
  box-shadow: 1.75px 1.75px 7px 0.875px rgba(0, 0, 0, 0.5);
}

.shadow_5 {
