}

.shadow_n1 {
  box-shadow: inset 0.5px 0.5px 2px 0.25px rgba(0, 0, 0, 0.1);
}

.shadow_n2 {
  box-shadow: inset 1px 1px 4px 0.5px rgba(0, 0, 0, 0.2);
}

.shadow_n3 {
  box-shadow: inset 1.5px 1.5px 6px 0.75px rgba(0, 0, 0, 0.3);
}

.shadow_n4 {
